import React, {useState, useEffect} from 'react'
import { Link, useLocation, useNavigate } from 'react-router-dom'
import { Form, Button, Row, Col } from 'react-bootstrap'
import { useDispatch, useSelector } from 'react-redux'
import Loader from '../components/Loader'
import Message from '../components/Message'
import { login } from '../actions/userActions'
import FormContainer from '../components/FormContainer'

function LoginScreen() {
    const [email, setEmail] = useState("")
    const [password, setPassword] = useState("")
    const dispatch = useDispatch()
    const location = useLocation()
    const navigate = useNavigate()

    //his catches the redirect in the url
    const redirect = location.search ? `/${location.search.split('=')[1]}` : '/'
    // grabbing the userLogin State from the store.js
    const userLogin = useSelector(state => state.userLogin)
    const {loading, error, userInfo} = userLogin

    useEffect(()=>{
        //if user is logged in, redirect them away from this page since there is no need to login again
        if(userInfo) {
            navigate(redirect)
        }
    }, [navigate, userInfo, redirect])

    const submitHandler = (e) => {
        e.preventDefault()
        //DISPATCH LOGIN
        dispatch(login(email,password))
    }

  return (
    <FormContainer>
      <h1>Sign In</h1>
      {
        error && <Message variant='danger'>{error}</Message>
      }
      {
        loading && <Loader />
      }
      <Form onSubmit={submitHandler}>
        <Form.Group controlId='email'>
            <Form.Label>Email Address</Form.Label>
            <Form.Control 
                type='email' 
                placeholder='Enter email' 
                value={email} 
                onChange={(e) => setEmail(e.target.value)}
            ></Form.Control>
        </Form.Group>
        <Form.Group controlId='password'>
            <Form.Label>Password</Form.Label>
            <Form.Control 
                type='password' 
                placeholder='Enter password' 
                value={password} 
                onChange={(e) => setPassword(e.target.value)}
            ></Form.Control>
        </Form.Group>
        <Button type='submit' variant='primary' className='my-2'>
            Sign In
        </Button>

      </Form>
      <Row className='py-3'>
        <Col>
            New Customer? {' '}
            <Link to={redirect ? `/register/${redirect}` : '/register'}>
                Register
            </Link>
        </Col>
      </Row>
    </FormContainer>
  )
}

export default LoginScreen
