import React, { useEffect } from 'react'
import { Link, useParams, useNavigate } from 'react-router-dom'
import { useDispatch, useSelector } from 'react-redux'
import { Row, Col, ListGroup, Image, Form, Button, Card } from 'react-bootstrap'
import Message from '../components/Message'
import { addToCart, removeFromCart } from '../actions/cartActions'

function CartScreen() {
    const params = useParams()
    const navigate = useNavigate()
    const dispatch = useDispatch()

    const productId = params.id
    // /cart/1?qty=1 --> qty=1 --> split at = take index 1
    const qty = window.location.search ? Number(window.location.search.split('=')[1]) : 1

    const cart = useSelector(state => state.cart)
    const { cartItems } = cart
    
    useEffect(() => {
        if (productId) {
            dispatch(addToCart(productId, qty))
        }
    }, [dispatch, productId, qty])

    const removeFromCartHandler = (id) => {
        dispatch(removeFromCart(id))
    }   
    // reduce((acc, item) => acc + item.qty * item.price, 0) 
    // acc is accumulator starts at 0, for each item it loops over all the items and adds the qty * price together
    // add qty * price to acc
    // final result is total price
  return (
    <Row>

        <Col md={8}>
            <h1>Shopping Cart</h1>
            {cartItems.length === 0 ? (
                <Message>Your cart is empty <Link to="/">Go Back</Link></Message>
            ) : (   
                <ListGroup variant='flush'>
                    {cartItems.map(item => (
                        <ListGroup.Item key={item.product}>
                            <Row>
                                <Col md={2}>
                                    <Image src={item.image} alt={item.name} fluid rounded />
                                </Col>
                                <Col md={3}>
                                    <Link to={`/product/${item.product}`}>{item.name}</Link>
                                </Col>
                                <Col md={2}>${item.price}</Col>
                                <Col md={3}>
                                    <Form.Control 
                                        as="select" 
                                        value={item.qty}
                                        onChange={(e) => 
                                            dispatch(addToCart(item.product, Number(e.target.value)))
                                        }   
                                    >
                                        {[...Array(item.countInStock).keys()].map(x => (
                                            <option key={x + 1} value={x + 1}>{x + 1}</option>
                                        ))}
                                    </Form.Control> 
                                </Col>
                                <Col md={2}>
                                    <Button 
                                        type='button' 
                                        variant='light' 
                                        onClick={() => removeFromCartHandler(item.product)}
                                    >
                                        <i className='fas fa-trash'></i>
                                    </Button>
                                </Col>
                            </Row>
                        </ListGroup.Item>
                    ))}
                </ListGroup>
            )}
        </Col>
        <Col md={4}>
            <Card>
                <ListGroup variant='flush'>
                    <ListGroup.Item>
                        <h2>
                            Subtotal ({cartItems.reduce((acc, item) => acc + item.qty, 0)}) items
                        </h2>
                        $
                        {cartItems
                            .reduce((acc, item) => acc + item.qty * item.price, 0)
                            .toFixed(2)
                        }   
                    </ListGroup.Item>
                    <ListGroup.Item>
                        <Button 
                            type='button'
                            className='btn-block'
                            disabled={cartItems.length === 0}
                            onClick={() => navigate('/login?redirect=shipping')}//Great way to check if user is logged in / redirect
                        >
                            Proceed To Checkout
                        </Button>
                    </ListGroup.Item>
                </ListGroup>
            </Card>
        </Col>
    </Row>
  )
}

export default CartScreen
